import { NextRequest, NextResponse } from "next/server";
import { Resend } from "resend";

const resend = new Resend(process.env.RESEND_API_KEY);

type Body = { name?: string; email?: string; message?: string };

export async function POST(req: NextRequest) {
  try {
    const { name, email, message } = (await req.json()) as Body;

    if (!name || !email || !message) {
      return NextResponse.json(
        { ok: false, error: "Missing fields" },
        { status: 400 }
      );
    }

    const from = process.env.EMAIL_FROM ?? "Portfolio <no-reply@example.com>";
    const to = process.env.EMAIL_TO ?? "you@example.com";

    const { error } = await resend.emails.send({
      from,
      to,
      subject: `New message from ${name}`,
      replyTo: email, // âœ… not reply_to
      text: `Name: ${name}\nEmail: ${email}\n\n${message}`,
    });

    if (error) {
      return NextResponse.json(
        { ok: false, error: String(error) },
        { status: 500 }
      );
    }

    return NextResponse.json({ ok: true });
  } catch (err: unknown) {
    const msg = err instanceof Error ? err.message : "Unexpected error";
    return NextResponse.json({ ok: false, error: msg }, { status: 500 });
  }
}
